}

body {
  background-color: var(--primary-light-color);
  color: var(--primary-dark-color);
  font-family: Manrope400;
  font-size: 16px;
  transition: all .5s cubic-bezier(.4, 0, .2, 1);
}

body.dark-theme {
  color: var(--primary-light-color);
  background-color: var(--dark-theme-body-bg-color);
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 16px;
  padding-right: 16px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition-property: opacity, visibility;
  transition-duration: var(--transition-duration);
  transition-timing-function: var(--transition-timing-function);
}

:root {
  --timing-function: .25s cubic-bezier(.4, 0, .2, 1);
  --primary-light-color: #f4f4f4;
  --primary-dark-color: #111321;
  --dark-theme-body-bg-color: #2e2e2e;
  --dropdown-bg-color: #f8f8f8;
  --secondary-light-color: #fff;
  --accent-secondary-color: #4440f6;
  --accent-primary-color: #4440f7;
  --accent-third-color: #4b48db;
  --accent-card-btn-bg-color: rgba(68, 64, 247, .7);
  --light-text-color: #a8a8a8;
  --light-theme-switcher-color: #5f6775;
  --weather-btn-color: rgba(254, 254, 255, .3);
  --main-font: "Manrope", sans-serif;
  --secondary-font: "Poppins", sans-serif;
  --third-font: "Roboto", sans-serif;
}

