  bottom: 18px;
  left: 16px;
}

@media screen and (max-width: 767px) {
  .theme-toggler.is-hidden {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .theme-toggler {
    margin-left: auto;
    position: static;
  }
}

.theme-toggler__pretext, .theme-toggler__posttext {
  display: none;
}

@media screen and (min-width: 1280px) {
  .theme-toggler__pretext, .theme-toggler__posttext {
    display: block;
  }
}

.theme-toggler__preinfo {
  color: #4b48d8;
  transition: color .5s cubic-bezier(.4, 0, .2, 1);
}

.theme-toggler.isActive .theme-toggler__preinfo {
  color: #5f6775;
}

.theme-toggler__postinfo {
  color: #5f6775;
  transition: color .5s cubic-bezier(.4, 0, .2, 1);
}

.theme-toggler.isActive .theme-toggler__postinfo {
  color: #4b48d8;
}

.theme-toggler__text {
  color: inherit;
  display: none;
}

@media screen and (min-width: 1280px) {
  .theme-toggler__text {
    display: block;
  }
}

.theme-toggler__icon {
  color: inherit;
  fill: currentColor;
}

@media screen and (min-width: 1280px) {
  .theme-toggler__icon {
    display: none;
  }
}

.theme-toggler__toggle-icon {
  height: 20px;
  width: 40px;
  border: 1px solid var(--accent-third-color);
  background-color: #f4f4f4;
  border-radius: 10px;
  transition: background-color .5s cubic-bezier(.4, 0, .2, 1), border .5s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.theme-toggler.isActive .theme-toggler__toggle-icon {
  background-color: #4b48d8;
  border: 1px solid #fff;
}

.theme-toggler__toggle-icon:after {
  content: "";
  height: 16px;
  width: 16px;
  background-color: #4b48d8;
  border-radius: 8px;
  transition: transform .5s cubic-bezier(.4, 0, .2, 1), background-color .5s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 1px;
  left: 1px;
  transform: translateX(0);
}

.theme-toggler.isActive .theme-toggler__toggle-icon:after {
  background-color: #fff;
  transform: translateX(20px);
}

